<!--  -->
<template>
  <div class="findPassword">
    <header-top>
      
    </header-top>
    <ul class="newAddress">
      <li v-if="checkFlag=='1'">
        <input type="text" class="name" placeholder="" v-model="userName">
      </li>
      <li v-if="checkFlag=='2'">
        {{pwdQuestion}}<input type="email" class="email" placeholder="" v-model="pwdAnswer">
      </li>
      <li v-if="checkFlag=='3'">
        <input type="password" class="password" placeholder="6~16" v-model="newPwd">
      </li>
    </ul>
    <div class="errTips">{{errTips}}</div>
    <div class="submit" @click="inspect"></div>
    <modal>
      <div class="content">
        {{modalText}}
      </div>
      <div class="button">
        <button @click="routerLogin">{{modalButton}}({{modalTime}})</button>
      </div>
    </modal>
  </div>
</template>

<script>
import HeaderTop from './Header'
import axios from 'axios'
import Modal from './Modal'

export default {
  data () {
    return {
      userName: '', // 
      pwdQuestion: '', // 
      pwdAnswer: '', // 
      checkFlag: '1', // 
      newPwd: '', // 
      modalText: '', // 
      modalButton: '', // 
      modalTime: 3, // 
      errTips: '' // 
    }
  },
  components: {
    HeaderTop,
    Modal
  },
  methods: {
    inspect() {
      // 
      if (this.checkFlag === '1') {
        this.checkQuestion()
      } else if (this.checkFlag === '2') {
        // 
        this.checkAnswer()
      } else if (this.checkFlag === '3') {
        // 
        this.resetPwd()
      }
    },
    // 
    checkQuestion() {
      if (!this.userName) {
        this.errTips = ''
        return false
      }
      this.errTips = ''
      var _this = this
      axios.post('/api/user/checkPwdQuestion', {
        userName: _this.userName
      }).then(function(res) {
        if (res.data.status === '0') {
          _this.pwdQuestion = res.data.result.pwdQuestion
          _this.checkFlag = '2'
        } else {
          _this.checkFlag = '1'
          _this.errTips = ''
        }
      })
    },
    // 
    checkAnswer() {
      this.errTips = ''
      var _this = this
      axios.post('/api/user/inspectPwdAnswer', {
        userName: _this.userName,
        pwdAnswer: _this.pwdAnswer
      }).then(function(res) {
        if (res.data.status === '0') {
          _this.checkFlag = '3'
        } else {
          _this.errTips = ''
          _this.checkFlag = '2'
        }
      })
    },
    // 
    resetPwd() {
      var passwordReg = /^\w{6,16}$/g.test(this.newPwd)
      if (!passwordReg) {
        this.errTips = ''
        return false
      }
      var _this = this
      axios.post('/api/user/newPwd', {
        userName: _this.userName,
        userPwd: _this.newPwd
      }).then(function(res) {
        if (res.data.status === '0') {
          _this.$store.commit('controlModal', true)
          _this.modalText = '!'
          _this.modalButton = ''
          // 
          var time = setInterval(function() {
            _this.modalTime--
            if (_this.modalTime <= 0) {
              clearInterval(time)
              _this.$store.commit('controlModal', false)
              _this.routerLogin()
            }
          }, 1000)
        } else {
          _this.checkFlag = '3'
        }
      })
    },
    // 
    routerLogin() {
      this.$store.commit('controlModal', false)
      this.$router.push('login')
    }
  },
  created() {
    this.$store.commit('changeLink', 'login')
  }
}
</script>
<style scoped>
.findPassword{
  margin-top: 100px;
  padding:0 30px;
}
.findPassword ul li{
  height: 120px;
  font-size: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-bottom: 1px solid #ccc;
}
.findPassword ul li input{
  width: 100%;
  height: 40px;
  font-size: 28px;
  border: none;
  outline: none;
  margin-top: 10px;
}
.findPassword .submit{
  height: 80px;
  line-height: 80px;
  background: #ff6700;
  -webkit-border-radius: 10px;
  border-radius: 10px;
  text-align: center;
  margin-top: 30px;
  color: #fff;
  font-size: 32px;
}
.findPassword .errTips{
  color: #ff6700;
  text-align: center;
  line-height: 30px;
  font-size: 28px;
  margin-top: 20px;
}
</style>



