<!--  -->
<template>
  <div class="register">
    <header-top>
      
    </header-top>
    <ul>
      <li>
        <div><span class="red">*</span></div>
        <input type="text" placeholder="6~18" class="username" v-model="username">
      </li>
      <li>
        <div><span class="red">*</span></div>
        <input type="password" placeholder="6~16" class="password" v-model="password">
      </li>
      <li>
        <div><span class="red">*</span></div>
        <input type="text" placeholder="13" v-model="mobile">
      </li>
      <li>
        <div><span class="red">*</span></div>
        <input type="text" placeholder="" v-model="pwdQuestion">
      </li>
      <li>
        <div><span class="red">*</span></div>
        <input type="text" placeholder="" v-model="pwdAnswer">
      </li>
    </ul>
    <div class="tips" v-if="tips">{{tips}}</div>
    <div class="submit" @click="inspect"></div>
    <div class="login"><router-link to="/login"></router-link></div>
    <modal>
      <div class="content">
        {{modalText}}
      </div>
      <div class="button">
        <button @click="routerLogin" v-if="registerFlag">({{modalTime}})</button>
        <button @click="closeModal" v-if="!registerFlag"></button>
      </div>
    </modal>
  </div>
</template>

<script>
import HeaderTop from './Header'
import Modal from './Modal'
import axios from 'axios'
export default {
  data () {
    return {
      username: '', // 
      password: '', // 
      mobile: '', // 
      pwdQuestion: '', // 
      pwdAnswer: '', // 
      tips: '', // 
      modalText: '', // 
      modalTime: 3, // 
      registerFlag: false // 
    }
  },
  components: {
    HeaderTop,
    Modal
  },
  methods: {
    inspect() {
      // 
      var usernameReg = /^\w{6,18}$/g.test(this.username)
      var passwordReg = /^\w{6,16}$/g.test(this.password)
      var mobileReg = /^1[3-9]\d{9}$/g.test(this.mobile)
      // 
      if (!usernameReg) {
        this.tips = ''
        return false
      } else {
        this.tips = ''
      }
      if (!passwordReg) {
        this.tips = ''
        return false
      } else {
        this.tips = ''
      }
      if (!mobileReg) {
        this.tips = ''
        return false
      } else {
        this.tips = ''
      }
      if (usernameReg && passwordReg && mobileReg) {
        this.register()
      }
    },
    // 
    register() {
      var _this = this
      axios
        .post('/api/user/addUsers', {
          // 
          userName: _this.username,
          userPwd: _this.password,
          mobile: _this.mobile,
          pwdQuestion: _this.pwdQuestion,
          pwdAnswer: _this.pwdAnswer
        })
        .then(function(res) {
          if (res.data.status === '0') {
            // 
            _this.$store.commit('controlModal', true)
            _this.modalText = '!'
            _this.registerFlag = true
            var time = setInterval(function() {
              // 3
              _this.modalTime--
              if (_this.modalTime <= 0) {
                clearInterval(time)
                _this.$store.commit('controlModal', false)
                _this.routerLogin()
              }
            }, 1000)
          } else {
            _this.$store.commit('controlModal', true)
            _this.modalText = res.data.msg
            _this.registerFlag = false
          }
        })
    },
    // 
    routerLogin() {
      this.$router.push('login')
      this.$store.commit('controlModal', false)
    },
    // 
    closeModal() {
      this.$store.commit('controlModal', false)
      this.$store.commit('changeLink', '')
    }
  }
}
</script>
<style scoped>
.register{
  margin-top: 100px;
  padding:0 30px;
}
.register ul li{
  height: 120px;
  font-size: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-bottom: 1px solid #ccc;
}
.register ul li input{
  width: 100%;
  height: 40px;
  font-size: 28px;
  border: none;
  outline: none;
  margin-top: 10px;
}
.register ul li .red{
  color: #ff0000;
  margin:0 5px;
}
.register .tips{
  color: #ff6700;
  text-align: center;
  font-size: 30px;
  height: 50px;
  line-height: 50px;
  margin: 30px 0;
}
.register .submit{
  height: 80px;
  line-height: 80px;
  background: #ff6700;
  border-radius: 10px;
  text-align: center;
  color: #fff;
  font-size: 32px;
}
.register .login{
  height: 80px;
  line-height: 80px;
  font-size: 26px;
  text-align: right;
}
.register .login a{
  color: #666;
}
</style>



